.profile__info {
  margin-left: 30px;
  display: grid;
  grid-template-columns: repeat(2, minmax(min-content, max-content));
  align-items: center;
  gap: 16px 18px;
}

@media screen and (max-width: 900px) {
  .profile__info {
    margin: 0;
    padding-top: 26px;
    gap: 12px 10px;
  }
}
